                <el-breadcrumb separator="/">
                    <el-breadcrumb-item :to="{path:'/base'}"><i class="el-icon-menu" style="marginRight:5px;"></i></el-breadcrumb-item>
                    <el-breadcrumb-item :to="{path:'/cplist'}"></el-breadcrumb-item>
                    <el-breadcrumb-item></el-breadcrumb-item>
                </el-breadcrumb>
            </div>-->
            <div class="edit-oprate">
                <p class="small-title">/</p>
                <el-form :label-position="labelPosition" :model="editData" label-width="100px" :inline="true">
                    <p class="form-tab-label"></p>
                    <el-form-item label="">
                        <el-input v-model="editData.Id" placeholder="" style="width:400px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <template scope="scope">
                            <el-select v-model="editData.brand" placeholder="" style="width:400px;">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </template>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.proname" placeholder="" style="width:400px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.proNextName" placeholder="" style="width:400px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.barcode" placeholder="" style="width:400px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
