                            <el-select v-model="editData.protype" placeholder="" style="width:400px;">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </template>
                    </el-form-item>
                    <el-form-item label="">
                        <template scope="scope">
                            <el-select v-model="editData.delivery" placeholder="" style="width:400px;">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </template>
                    </el-form-item>
                    <el-form-item label="">
                        <template scope="scope">
                            <el-select v-model="editData.unit" placeholder="" style="width:400px;">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </template>
                    </el-form-item>
                   <el-form-item label="">
                        <template scope="scope">
                            <el-select v-model="editData.sellState" placeholder="" style="width:400px;">
                                <el-option label="" value="on"></el-option>
                                <el-option label="" value="no"></el-option>
                                <el-option label="" value="down"></el-option>
                            </el-select>
                        </template>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.sellnum" placeholder="" style="width:400px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.proaddress" placeholder="" style="width:400px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.prosort" placeholder="" style="width:400px;"></el-input>
                    </el-form-item>
                    <p class="form-tab-label"></p>
                    <el-form-item label="">
                        <el-input v-model="editData.prokeyword" placeholder="" style="width:800px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.shortms" placeholder="" style="width:800px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input type="textarea" :rows="4" v-model="editData.productms" resize="none" style="width:800px;" placeholder=""></el-input>
                    </el-form-item>
                    <p class="form-tab-label"></p>
                    <el-form-item label="">
                        <el-input v-model="editData.sellxj" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.sellyj" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.sellfxjg" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="editData.nowrobnum" placeholder=""></el-input>
                    </el-form-item>
                   <el-form-item label="">
                        <template scope="scope">
                            <el-select v-model="editData.rebate" placeholder="" style="width:400px;">
                                <el-option label="" value="noRebate"></el-option>
                                <el-option label="5%" value="5"></el-option>
                                <el-option label="10%" value="10"></el-option>
                            </el-select>
