                            <el-radio class="radio" v-model="radio" label="hotsell"></el-radio>
                            <el-radio class="radio" v-model="radio" label="Recommend"></el-radio>
                        </template>
                    </el-form-item>
                    <p class="form-tab-label"></p>
                    <div class="pic-box">
