<script>
    export default{
        name:"edit",
        data(){
            return{
                labelPosition:"right",
                radio:3,
                editData:{
                     Id:"123456",
                     brand:"",
                     proname:"",
                     proNextName:"",
                     barcode:"", 
                     protype:'',
                     delivery:"",
                     unit:"",
                     sellState:"",
                     sellnum:"",
                     proaddress:"",
                     prosort:"",
                     prokeyword:"",
                     shortms:"",
                     productms:"",
                     sellxj:'',
                     sellyj:"",
                     sellfxjg:"",
                     nowrobnum:"",
                     rebate:""
                },
                dialogImageUrl: '',
                dialogVisible: false
            }
        },
        methods:{
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            }
        }
    }
</script>
<style scoped>
.edit-oprate{
    padding-bottom:80px;
}
    .sptpbt{
        height: 40px;
        line-height: 40px;
        color: dodgerblue;
    } 
    .pic-box{
        margin-bottom: 10px;
    }
</style>


