                <el-form :inline="true" :model="supplierproductData" label-width="80px" :label-position="labelposition">
                    <el-form-item label="">
                        <el-input v-model="supplierproductData.proNumber" placeholder="" style="width:200px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="supplierproductData.proName" placeholder="" style="width:200px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="supplierproductData.supplier" placeholder="">
                            <el-option label="" value="allSupplier"></el-option>
                            <el-option label="" value="azzy"></el-option>
                            <el-option label="" value="szqhfd"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="supplierproductData.state" placreholder="">
