                        supplyPrice:"8.88",
                        addTime:"2017-2-4",
                        supplyNum:"5222",
                        stoNum:"222",

                    }
                ]
            }
        }
    }
</script>
<style lang="">
    .table-top{
        height: 40px;
        line-height: 40px;
        margin-bottom:10px;
    }
</style>


