<template>
    <div class="box-out">
        <div class="orderList">
            <div class="orderList-content">
                <p class="pro-sm-title"></p>
                <el-form :inline="true" :model="storeOrderData" label-width="80px" :label-position="labelposition">
                    <el-form-item label="">
                        <el-input v-model="storeOrderData.orderNum" placeholder="" style="width:200px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="storeOrderData.memberNum" placeholder="" style="width:200px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-col :span="11">
                            <el-date-picker type="date" placeholder="" v-model="storeOrderData.startDate" style="width: 100%;"></el-date-picker>
                        </el-col>
                        <el-col class="line" :span="2" style="text-align:center;">-</el-col>
                        <el-col :span="11">
                            <el-date-picker type="date" placeholder="" v-model="storeOrderData.endDate" style="width: 200px;"></el-date-picker>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="storeOrderData.orderState" placeholder="" style="width:200px;">
                            <el-option label="" value="bfd"></el-option>
                            <el-option label="" value="zbbf"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="storeOrderData.consignee" placeholder="" style="width:200px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="storeOrderData.telephone" placeholder="" style="width:200px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="storeOrderData.achievement" placeholder="" style="width:200px;">
                            <el-option label="" value="ml"></el-option>
                            <el-option label="" value="qhd"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" style="width:100px;"></el-button>
                    </el-form-item>
                </el-form>
                <el-row :gutter="20" class="table-top">
                    <el-col :span="4"><div class=""><span style="color:red;">{{selltotal}}</span></div></el-col>
                    <el-col :span="4"><div class=""><span style="color:red;">{{sellnow}}</span></div></el-col>
                    <el-col :span="16" style="text-align:right;">
                        <el-dropdown menu-align="start" trigger="click">
                            <el-button type="primary">
                                <i class="el-icon-caret-bottom el-icon--right"></i>
                            </el-button>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="a">Excel</el-dropdown-item>
                                <el-dropdown-item command="b"></el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </el-col>
                </el-row>
                <el-table :data="storeorderTable" style="width:100%;" border >
                    <el-table-column type="selection"align="center"></el-table-column>
                    <el-table-column label="" prop="orderNum" align="center"></el-table-column>
                    <el-table-column label="" prop="waybillNum" align="center"></el-table-column>
                    <el-table-column label="" prop="memberName"  align="center"></el-table-column>
                    <el-table-column label="" prop="telephone" align="center"></el-table-column>
                    <el-table-column label="" prop="consignee" align="center"></el-table-column>
                    <el-table-column label="" prop="payment" align="center"></el-table-column>
                    <el-table-column label="" prop="orderCash" align="center"></el-table-column>
                    <el-table-column label="" prop="states" align="center"></el-table-column>
                    <el-table-column label="" prop="ordertime" align="center"></el-table-column>
                    <el-table-column label="" prop="paytime" align="center"></el-table-column>
                    <el-table-column label="" align="center">
                        <template scope="scope">
                            <el-button type="text"></el-button>
                            <el-button type="text"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                labelposition:"right",
                storeOrderData:{
                    orderNum:"",
                    memberNum:"",
                    startDate:"",
                    endDate:"",
                    orderState:"",
                    consignee:"",
                    telephone:"",
                    achievement:"",
                },
                selltotal:"20031451",
                sellnow:"456789",
                storeorderTable:[
                    {
                        orderNum:"123456",
                        waybillNum:"253336",
                        memberName:"13465896589",
                        telephone:"13665896589",
                        consignee:"",
                        payment:"23.36",
                        orderCash:"12356",
                        states:"",
                        ordertime:"2017-2-4",
                        paytime:"2017-12-1"


                    }
                ]
            }
        }
    }
</script>
<style lang="">
    .table-top{
        height: 40px;
        line-height: 40px;
        margin-bottom:10px;
    }
</style>


