<template>
    <div class="box-out">
        <div id="list">
            <!--<div class="crumbs">
                <el-breadcrumb separator="/">
                    <el-breadcrumb-item :to="{path:'/base'}"><i class="el-icon-menu" style="marginRight:5px;"></i></el-breadcrumb-item>
                    <el-breadcrumb-item></el-breadcrumb-item>
                </el-breadcrumb>
            </div>-->
            <div class="prolist">
                <p class="pro-sm-title"></p>
                <el-form :inline='true' :model="SearchCondation" class="demo-form-inline">
                    <el-form-item label="">
                        <el-input v-model="SearchCondation.proName" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="SearchCondation.proId" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="SearchCondation.proType" placeholder="">
                            <el-option label="" value="Baby"></el-option>
                            <el-option label="" value="overseas"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary"></el-button>
                    </el-form-item>
                    <el-dropdown menu-align="start" trigger="click"  @command="routeEdit">
                        <el-button type="primary">
                            <i class="el-icon-caret-bottom el-icon--right"></i>
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="a"></el-dropdown-item>
                            <el-dropdown-item command="b">Excel</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-form>
                <div class="prolist-table">
                    <el-table :data="prolist" style="width:100%; " border >
                        <el-table-column type="selection"></el-table-column>
                        <el-table-column label="" prop="serialnum" width="70" align="center"></el-table-column>
                        <el-table-column label="" prop="proId" width="100" align="center"></el-table-column>
                        <el-table-column label="" prop="proName"  align="center" show-overflow-tooltip="true"></el-table-column>
                        <el-table-column label="" prop="proType" width="100" align="center"></el-table-column>
                        <el-table-column label="" prop="proPrice" width="90" align="center"></el-table-column>
                        <el-table-column label="" prop="barCode" width="150" align="center"></el-table-column>
                        <el-table-column label="" width="100" align="center">
                            <template scope="scope">
                                <el-button type="text"></el-button>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="addTime" width="120" align="center"></el-table-column>
                        <el-table-column label="" align="center">
                            <template scope="scope">
                                <el-button size="small" type="primary"></el-button>
                                <el-button size="small" type="primary"></el-button>
                                <el-button size="small" type="primary" @click="addStandard"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <template>
                        <div class="block">
                            <el-pagination :current-page="4" :page-sizes="[100, 200, 300, 400]" :page-size="100"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="1000">
                            </el-pagination>
                        </div>                       
                    </template>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default{
        name:'VvoList',
        data(){
            return{
                SearchCondation:{
                    proName:'',
                    proId:'',
                    proType:'',
                    region:""
                },
                prolist:[
                    {
                        serialnum:'1234',
                        proId:"456632",
                        proName:' Abbott  similac 4 900g (3)',
                        proType:'',
                        proPrice:"5625.00",
                        barCode:'4006387006044',
                        addTime:"2017-3-22"
                    }

                ],
                multipleSelection:[]
            }
        },
        methods:{
            handleSelectionChange:function(val){
                this.multipleSelection = val;
            },
            routeEdit:function(command){
                if(command == "a"){
                    this.$router.push('/cpedit');
                }
            },
            addStandard(){
                this.$router.push('/addstandard');
            }
        }
    }
</script>
<style scoped>

</style>


