                <el-form :inline="true" :model="purchaseOrderData" label-width="80px" :label-position="labelposition">
                    <el-form-item label="">
                        <el-input v-model="purchaseOrderData.batch" placeholder="" style="width:200px;"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-col :span="11">
                            <el-date-picker type="date" placeholder="" v-model="purchaseOrderData.startDate" style="width: 100%;"></el-date-picker>
                        </el-col>
                        <el-col class="line" :span="2" style="text-align:center;">-</el-col>
                        <el-col :span="11">
                            <el-date-picker type="date" placeholder="" v-model="purchaseOrderData.endDate" style="width: 200px;"></el-date-picker>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="purchaseOrderData.auditStatus" placeholder="" style="width:200px;">
                            <el-option label="" value="allexamine"></el-option>
                            <el-option label="" value="waitexamine"></el-option>
