                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="purchaseOrderData.supplier" placeholder="" style="width:200px;">
                            <el-option label="" value="all"></el-option>
                            <el-option label="" value="papagou"></el-option>
                            <el-option label="" value="haitun"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" style="width:100px;"></el-button>
                    </el-form-item>
                </el-form>
                <el-dropdown menu-align="start" trigger="click">
                    <el-button type="primary">
                        <i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="a"></el-dropdown-item>
                        <el-dropdown-item command="b"></el-dropdown-item>
                        <el-dropdown-item command="c"></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <el-table :data="purchaseOrderTable" style="width:100%;" border >
                    <el-table-column type="selection"align="center"></el-table-column>
