                    <el-table-column label="" prop="purchaseTime" align="center"></el-table-column>
                    <el-table-column label="" prop="auditStatus" align="center"></el-table-column>
                    <el-table-column label="" align="center">
                        <template scope="scope">
                            <el-button type="text"></el-button>
                            <el-button type="text"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
