                purchaseOrderData:{
                    batch:"",
                    startDate:"",
                    endDate:"",
                    auditStatus:"",
                    supplier:"",
                },
                purchaseOrderTable:[
                    {
                        batchNum:"123456",
                        paidtotal:"253336",
                        unpaidtotal:"13465896589",
                        purchaseTotal:"13665896589",
                        purchaser:"",
                        purchaseTime:"2017-2-4",
                        auditStatus:"",
                    }
                ]
            }
        }
    }
</script>
<style lang="">
    .table-top{
        height: 40px;
        line-height: 40px;
        margin-bottom:10px;
    }
</style>


