<template>
    <div class="box-out">
        <div id="protype">
            <!--<div class="crumbs">
                <el-breadcrumb separator="/">
                    <el-breadcrumb-item :to="{path:'/base'}"><i class="el-icon-menu" style="marginRight:5px;"></i></el-breadcrumb-item>
                    <el-breadcrumb-item></el-breadcrumb-item>
                </el-breadcrumb>
            </div> -->
            <div class="probrand">
                <p class="pro-sm-title"></p>
                <el-form :inline="true" :model="typeFormData">
                    <el-form-item label="">
                        <el-input v-model="typeFormData.formType" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="typeFormData.state" placeholder="">
                            <el-option label="" value="normal"></el-option>
                            <el-option label="" value="locked"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <template scope="scope">
                            <el-button type="primary"></el-button>
                        </template>
                    </el-form-item>
                </el-form>
                <el-table :data="brandData" border style="width:100%;">
                    <el-table-column type="selection" width="55"></el-table-column>
                    <el-table-column label="" prop="serialnum" align="center"></el-table-column>
                    <el-table-column label="" prop="fatherType" align="center"></el-table-column>
                    <el-table-column label="" prop="typeName" align="center"></el-table-column>
                    <el-table-column label="" prop="addTypeName" align="center"></el-table-column>
                    <el-table-column label="" align="center">
                        <template scope="scope">
                            <el-button type="text" icon="close"></el-button>
                        </template>
                    </el-table-column>
                    <el-table-column label="" align="center">
                        <template scope="scope">
                            <el-button type="text"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="block">
                <el-pagination
                    layout="prev, pager, next"
                    :total="100"
                    :page-size="10"
                    >
                </el-pagination>
                </div>
            </div>           
        </div>
    </div>
</template>
<script>
    export default{
        name:"probrand",
        data(){
            return{
                brandData:[
                    {
                        serialnum:"1234",
                        fatherType:"",
                        typeName:"",
                        addTypeName:"2017-3-22"

                    }
                ],
                typeFormData:{formBrandName:"",state:""}
            }
        }
    }
</script>
<style lang="">
    
</style>


